  height: 20px;
  margin: 0 10px;
  display: inline-block;
  position: relative;
}

.checkbox {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #4b48db;
  border-radius: 10px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #4440f6;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1.5px;
}

.checkbox:checked + .slider:before {
  transform: translateX(19px);
}

