.pagination--container--categories {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 60px;
    margin-top: 40px;
    font-family: Manrope;
    font-size: 14px;
    font-weight: 500;
    line-height: 19px;
    text-align: left;
    background-color: $main-bg-color;

    @media screen and (min-width: $tablet) and (max-width: 1279px) {
        height: 31px;
        margin-bottom: 100px;
        margin-top: 48px;
    }

    @media screen and (min-width: $desktop) {
        margin-bottom: 150px;
        margin-top: 60px;
    }
}

.pagin-categories {
    display: flex;
    justify-content: center;
    height: 28px;
    width: 300px;

    @media screen and (min-width: $tablet) {
        height: 31px;
        width: 500px;
    }
}

.page-container-categories a {
    padding: 4px 12px;
    font-family: Manrope;
    font-size: 14px;
    font-weight: 500;
    color: $main-text-color;

    .pg-link-dot {
        padding: 1px 1px;
    }
}

.page-container-categories a.pg-link-dot {
    padding: 0px 0px;
    font-family: Manrope;
    font-size: 14px;
    font-weight: 500;
    color: $main-text-color;
}

.pg-item-cat {

    :hover,
    :focus {
        color: $white-color;
        background-color: $second-active-color;
    }

    border: 1px solid $second-active-color;
    border-radius: 2px;
}

.pagination--container--categories button {
    display: flex;
    justify-content: center;
    cursor: pointer;
    background-color: transparent;
    border: none;
}

button.next-page-cat,
button.prev-page-cat {
    width: 72px;
    font-family: Manrope;
    font-size: 14px;
    font-weight: 500;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: left;
    border: 1px solid $second-active-color;
    border-radius: 2px;
}

button.prev-page-cat {
    margin-right: 6px;
}

button.next-page-cat {
    margin-left: 6px;
}

.pagination--container--categories button:disabled a {
    color: $main-text-color;
    font-family: Manrope;
    font-size: 14px;
    font-weight: 500;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: left;
}

#pagination-categories {
    display: flex;
    align-items: center;
}

#pagination-categories li {
    list-style: none;
    cursor: pointer;
    overflow: hidden;
    margin: 0 4px;
}

#pagination-categories li.active {
    background-color: $second-active-color;
}

#pagination-categories li.active a {
    color: var(--white-color);
}

#pagination-categories li a {
    text-decoration: none;
    padding: 4px auto;
    display: inline-block;
    font-family: Manrope;
    font-size: 14px;
    font-weight: 500;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: left;
    pointer-events: none;
    color: $main-text-color;
}

#pagination-categories li:hover {
    background-color: $second-active-color;
}

#pagination-categories li:hover a {
    color: var(--white-color);
    background-color: $second-active-color;
}

.settings-cat {
    padding-top: 3rem;
    display: flex;
    align-items: flex-start;
}

.settings-cat .form-input {
    margin-bottom: 1rem;
}

.settings-cat .form-input:not(:last-child) {
    margin-right: 3rem;
}

.settings-cat .form-input>label {
    margin-bottom: 10px;
}

.settings-cat .form-input label {
    font-family: Manrope;
    font-size: 14px;
    font-weight: 500;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: left;
    display: block;
}

.settings-cat .form-input>input {
    width: 100%;
}

.settings-cat .form-input input {
    font-size: 18px;
    outline: none;
    padding: 10px 17px;
    border: 1px solid #b6b6b6;
}

.settings-cat .wrapper-form-check {
    display: flex;
}

.settings-cat .wrapper-form-check .form-check {
    display: flex;
    line-height: 1;
    align-items: center;
}

.settings-cat .wrapper-form-check .form-check:not(:last-child) {
    margin-right: 10px;
}

.settings-cat .wrapper-form-check label {
    margin-left: 10px;
}

@media (max-width: 992px) {
    .settings-cat {
        flex-direction: column;
    }
}

.s_prew-cat {
    font-family: Manrope;
    font-size: 14px;
    font-weight: 500;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: left;
}

